@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap);
*{
    padding:0;
    box-sizing:border-box;
    margin:0;
    font-family: 'Montserrat', sans-serif;
    transition:  .3s ease;

}

 /*Default colors and styles*/
:root{
    --PrimaryColor: hsl(94, 59%, 35%);

    --HoverColor:#bdf094;
    --paleGreen:hsl(96,75%,89%);
    --whiteColor:hsl(0,0%,100%);
    --blackColor:hsl(0,0%,18%);
    --textColor:hsl(240,1%,48%);
    --bgColor:hsl(220,10%,94%);
    --grayText:rgb(190,190,190);
    --inputColor:hsl(330,12%,97%);



    --biggestFontSize: 2.5rem;
    --h1FontSize:1.5rem;
    --h2FontSize:1.25rem;
    --h3FontSize:1rem;
    --normalFontSize:.938rem;
    --smallFontSize: .813rem;
    --smallesFontSize:.75rem;
}

/*Default style*/

html{
    font-size:90%;
}

a{
    text-decoration: none;
}

li{
    list-style: none;
}

.icon{
    font-size: 1.25rem;
    font-size: var(--h2FontSize);
    cursor: pointer;
}


img{
    width: 100%;
    height: auto;

}
.flex{
    display: flex;
    align-items: center;
}

.grid{
    display: grid;
    align-items: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}


.btn{
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 8px;
    box-shadow: 0 2px 4px var(var(--inputColor));
    padding: .8rem 1rem;
    color: hsl(240,1%,48%);
    color: var(--textColor);


}

.btn .icon{
    margin-left:.5rem;

}

.btn:hover{
    background: hsl(96,75%,89%);
    background: var(--paleGreen);
    color: hsl(94, 59%, 35%);
    color: var(--PrimaryColor);

}

body{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 1rem;
}

.container{
    position: relative;
    width: 95vw;
    height: 95vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: hsl(220,10%,94%);
    background: var(--bgColor);
    overflow: hidden;
    box-shadow: 2px 2px 8px rgb(190,190,190);
    box-shadow: 2px 2px 8px var(--grayText);
    
}

/*hidden scroll */
::-webkit-scrollbar{
    display: none;
}
/*Default colors and styles*/
:root {
  --PrimaryColor: hsl(94, 59%, 35%);
  --HoverColor:#bdf094;
  --paleGreen:hsl(96,75%,89%);
  --whiteColor:hsl(0,0%,100%);
  --blackColor:hsl(0,0%,18%);
  --textColor:hsl(240,1%,48%);
  --bgColor:hsl(220,10%,94%);
  --grayText:rgb(190,190,190);
  --inputColor:hsl(330,12%,97%);
  --biggestFontSize: 2.5rem;
  --h1FontSize:1.5rem;
  --h2FontSize:1.25rem;
  --h3FontSize:1rem;
  --normalFontSize:.938rem;
  --smallFontSize: .813rem;
  --smallesFontSize:.75rem;
}

.sideBar {
  background: hsl(0,0%,100%);
  background: var(--whiteColor);
  box-shadow: 1px 0px 4px rgb(190,190,190);
  box-shadow: 1px 0px 4px var(--grayText);
  width: 18%;
  height: 100%;
  grid-gap: 3rem;
  gap: 3rem;
  overflow: auto;
}
.sideBar .logoDiv {
  padding: 1.5rem 1.5rem 0;
  cursor: pointer;
}
.sideBar .logoDiv img {
  max-width: 20px;
  margin-right: 0.5rem;
  background: hsl(330,12%,97%);
  background: var(--inputColor);
  border-radius: 2px;
  padding: 0.2rem;
}
.sideBar .logoDiv h2 {
  font-size: 1.25rem;
  font-size: var(--h2FontSize);
  color: hsl(94, 59%, 35%);
  color: var(--PrimaryColor);
}
.sideBar .menuDiv .divTitle, .sideBar .settingsDiv .divTitle {
  font-size: 1rem;
  font-size: var(--h3FontSize);
  color: hsl(0,0%,18%);
  color: var(--blackColor);
  padding: 0 1.5rem 1.5rem;
}
.sideBar .menuDiv .menuLists .listItem, .sideBar .settingsDiv .menuLists .listItem {
  padding: 0.2rem 1.5rem;
  width: 100%;
  position: relative;
}
.sideBar .menuDiv .menuLists .listItem::before, .sideBar .settingsDiv .menuLists .listItem::before {
  position: absolute;
  content: "";
  height: 0%;
  left: 0;
  width: 5px;
  bottom: 0;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  background: hsl(94, 59%, 35%);
  background: var(--PrimaryColor);
}
.sideBar .menuDiv .menuLists .listItem .menuLink, .sideBar .settingsDiv .menuLists .listItem .menuLink {
  color: rgb(190,190,190);
  color: var(--grayText);
  font-weight: 500;
  transition: 0;
}
.sideBar .menuDiv .menuLists .listItem .menuLink .icon, .sideBar .settingsDiv .menuLists .listItem .menuLink .icon {
  margin-right: 0.5rem;
}
.sideBar .menuDiv .menuLists .listItem:hover .menuLink, .sideBar .settingsDiv .menuLists .listItem:hover .menuLink {
  color: hsl(94, 59%, 35%);
  color: var(--PrimaryColor);
  transition: 0;
}
.sideBar .menuDiv .menuLists .listItem:hover::before, .sideBar .settingsDiv .menuLists .listItem:hover::before {
  height: 100%;
  transition: 0.3s ease;
}
.sideBar .sideBarCard {
  width: 100%;
  padding: 1rem;
  text-align: center;
  position: relative;
}
.sideBar .sideBarCard .icon {
  position: absolute;
  background: #bdf094;
  background: var(--HoverColor);
  border: 10px solid hsl(0,0%,100%);
  border: 10px solid var(--whiteColor);
  font-size: 3rem;
  border-radius: 50%;
  top: -8px;
  right: 50%;
  -webkit-transform: translate(50%);
          transform: translate(50%);
  z-index: 100;
}
.sideBar .sideBarCard .cardContent {
  position: relative;
  padding: 1rem;
  background: #bdf094;
  background: var(--HoverColor);
  border-radius: 10px;
  overflow: hidden;
}
.sideBar .sideBarCard .cardContent h3 {
  font-size: 1rem;
  font-size: var(--h3FontSize);
  margin-top: 1rem;
  padding: 1rem 0;
  font-weight: 800;
  color: hsl(0,0%,18%);
  color: var(--blackColor);
}
.sideBar .sideBarCard .cardContent p {
  font-size: .938rem;
  font-size: var(--normalFontSize);
  color: hsl(240,1%,48%);
  color: var(--textColor);
  padding-bottom: 1rem;
  font-weight: 500;
}
.sideBar .sideBarCard .cardContent .btn {
  position: relative;
  color: hsl(240,1%,48%);
  color: var(--textColor);
  z-index: 1000;
}
.sideBar .sideBarCard .cardContent .circle1, .sideBar .sideBarCard .cardContent .circle2 {
  position: absolute;
  background: hsl(96,75%,89%);
  background: var(--paleGreen);
  border-radius: 50%;
  opacity: 0.7;
}
.sideBar .sideBarCard .cardContent .circle1 {
  height: 100px;
  width: 100px;
  top: -50px;
  right: -50px;
  left: -50px;
}
.sideBar .sideBarCard .cardContent .circle2 {
  height: 150px;
  width: 150px;
  bottom: -80px;
  right: -70px;
  z-index: 1;
}/*# sourceMappingURL=sidebar.css.map */
